import { Fraction, fraction } from "mathjs";
import {
  FormulaText,
  FormulaTextPart,
  formulaTextToSpreadAblePart,
  Subscript,
  symbolToFormulaText,
} from "../formulaText";
import { Ion } from "./ions";

export class IonicBond {
  anion: Ion;
  cation: Ion;
  private anionToCationRatio: Fraction;

  constructor(anion: Ion, cation: Ion) {
    this.anion = anion;
    this.cation = cation;
    this.anionToCationRatio = fraction(cation.charge, anion.charge);
  }

  getAnionCount() {
    return this.anionToCationRatio.n;
  }

  getCationCount() {
    return this.anionToCationRatio.d;
  }

  getFormula(): FormulaTextPart[] {
    return ionSymbolsAndCountsToForumla(
      { symbol: this.anion.symbol, count: this.getAnionCount() },
      { symbol: this.cation.symbol, count: this.getCationCount() }
    );
  }

  getName() {
    return this.cation.name + " " + this.anion.name;
  }

  equals(molecule: IonicBond): boolean {
    return this.anion === molecule.anion && this.cation === molecule.cation;
  }
}

type SymbolAndCount = { symbol: string; count: number };
export function ionSymbolsAndCountsToForumla(
  anion: SymbolAndCount,
  cation: SymbolAndCount
) {
  return [cation, anion].flatMap((ion) =>
    formulaTextToSpreadAblePart(
      ionSymbolAndCountToHalfFormula(ion.symbol, ion.count)
    )
  );
}

export function ionSymbolAndCountToHalfFormula(
  symbol: string,
  count: number
): FormulaText {
  const formatedSymbol = symbolToFormulaText(symbol);

  const countSubscript = countToSubscript(count);

  if (countSubscript === "") {
    return formatedSymbol;
  }

  if (formatedSymbol.length === 1) {
    return [formatedSymbol[0], countSubscript];
  }

  return ["(", ...formatedSymbol, ")", countSubscript];
}

// turn numbers into string. ex. 1 => "", 2 => "2"
export function countToSubscript(count: number): FormulaTextPart {
  return count > 1 ? new Subscript(count.toString()) : "";
}
