import { CheckRounded, CloseRounded, ExpandMore } from "@mui/icons-material";
import DeleteIcon from "@mui/icons-material/Delete";
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  Typography,
} from "@mui/material";
import { useReducer, useState } from "react";
import { formulaTextToReactNode } from "../formulaText";
import { QuestionData } from "../questions/Question";

const NUMBER_OF_ANSWERS_TO_SHOW_AT_ONCE = 20;

export type AnsweredQuestion = {
  data: QuestionData;
  answer: number;
};

export function isQuestionCorrect(question: AnsweredQuestion): boolean {
  return question.answer === question.data.correctAnswerLocation;
}

type HistoryProps = {
  pastQuestions: AnsweredQuestion[];
  handleDelete: () => void;
};

export function HistoryPage(props: HistoryProps) {
  const [answersToShow, incrementAnswersToShow] = useReducer(
    (x) => x + NUMBER_OF_ANSWERS_TO_SHOW_AT_ONCE,
    NUMBER_OF_ANSWERS_TO_SHOW_AT_ONCE
  );

  const Container = (props: { children: any }) => (
    <Box sx={{ mx: "auto", maxWidth: 1000 }} className="page history-page">
      {props.children}
    </Box>
  );

  if (props.pastQuestions.length === 0) {
    return (
      <Container>
        <Typography className="page">No questions answered yet</Typography>
      </Container>
    );
  }

  const correctCount = props.pastQuestions.filter(
    (pastQuestion) =>
      pastQuestion.answer === pastQuestion.data.correctAnswerLocation
  ).length;

  return (
    <Container>
      <Box sx={{ display: "flex", justifyContent: "space-between" }}>
        <Box>
          <Typography>
            You have gotten {correctCount} right out of{" "}
            {props.pastQuestions.length}
          </Typography>
          <Typography>
            Accuracy:{" "}
            {Math.round((correctCount / props.pastQuestions.length) * 100)}%
          </Typography>
        </Box>
        <DeleteButton handleDelete={props.handleDelete} />
      </Box>
      <Box sx={{ my: 1 }} className="past-question-history">
        {props.pastQuestions
          .slice()
          .reverse()
          .slice(0, answersToShow)
          .map((question, i) => (
            <AnsweredQuestionElement key={i} question={question} />
          ))}
      </Box>
      <Box sx={{ display: "flex", gap: "4px", alignItems: "center" }}>
        {answersToShow < props.pastQuestions.length && (
          <Button variant="outlined" onClick={incrementAnswersToShow}>
            Load more
          </Button>
        )}
        <Typography>
          Showing {Math.min(props.pastQuestions.length, answersToShow)} out of{" "}
          {props.pastQuestions.length}.
        </Typography>
      </Box>
    </Container>
  );
}

function AnsweredQuestionElement({ question }: { question: AnsweredQuestion }) {
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMore />}
        className="answered-question-bar"
      >
        {question.answer === question.data.correctAnswerLocation ? (
          <CheckRounded color="success" />
        ) : (
          <CloseRounded color="error" />
        )}
        <Typography>{formulaTextToReactNode(question.data.prompt)}</Typography>
      </AccordionSummary>
      <AccordionDetails className="answered-question-info">
        <Typography>
          Correct answer: {formulaTextToReactNode(question.data.answer)}
        </Typography>
        <Typography>
          Your answer:{" "}
          {formulaTextToReactNode(question.data.finalList[question.answer])}
        </Typography>
      </AccordionDetails>
    </Accordion>
  );
}

type DeleteButtonProps = {
  handleDelete: () => void;
};
function DeleteButton(props: DeleteButtonProps) {
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <Button
        sx={{ width: 0 }}
        onClick={handleClickOpen}
        variant="contained"
        color="error"
      >
        <DeleteIcon />
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Erase question history?"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            This is irreversible.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            Cancel
          </Button>
          <Button
            onClick={() => {
              handleClose();
              props.handleDelete();
            }}
            color="error"
            variant="contained"
          >
            Confirm
          </Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
