import { fraction } from "mathjs";
import { FormulaText } from "../formulaText";
import { IonicBond, ionSymbolsAndCountsToForumla } from "../ions/ionTools";
import {
  Ion,
  metalCations,
  polyAtomicAnions,
  polyAtomicCations,
  simpleAnions,
  simpleCations,
} from "../ions/ions";
import { getRandom, getRandomSelection } from "../utils";
import { QuestionData } from "./Question";
import { QuestionChoices, QuestionSettings } from "./Settings";

export type QuestionGenerator = (
  molecule: IonicBond,
  anionList: Ion[],
  cationList: Ion[]
) => QuestionData;

const answerOptions = 6;
const maxCharge = 3;

// generate the 9 different combinations for the charge ratios
const countOptions = Array.from({ length: maxCharge * maxCharge }, (_, i) => i)
  .map((n) => ({ anion: (n % 3) + 1, cation: Math.floor(n / 3) + 1 }))
  .map(({ anion, cation }) => {
    const ratio = fraction(anion, cation);
    return { anion: ratio.d, cation: ratio.n };
  })
  // make sure we don't have duplicates
  .filter(
    ({ anion, cation }, index, arr) =>
      arr.findIndex((elm) => elm.anion === anion && elm.cation === cation) ===
      index
  );

export function createForumulaQuestion(molecule: IonicBond): QuestionData {
  const wrongAnswers: FormulaText[] = getRandomSelection(
    countOptions.filter(
      (counts) =>
        !(
          counts.anion === molecule.getAnionCount() &&
          counts.cation === molecule.getCationCount()
        )
    ),
    answerOptions - 1
    // create the formula
  ).map((counts) =>
    ionSymbolsAndCountsToForumla(
      { symbol: molecule.anion.symbol, count: counts.anion },
      { symbol: molecule.cation.symbol, count: counts.cation }
    )
  );

  return new QuestionData({
    prompt: `What is the formula for ${molecule.getName()}?`,
    answer: molecule.getFormula(),
    wrongAnswers,
  });
}

export function createNameQuestion(
  molecule: IonicBond,
  anionList: Ion[],
  cationList: Ion[]
): QuestionData {
  const anions = getRandomSelection(
    anionList.filter((ion) => ion !== molecule.anion),
    2
  ).concat(molecule.anion);
  const cations = getRandomSelection(
    cationList.filter((ion) => ion !== molecule.cation),
    2
  ).concat(molecule.cation);

  const moleculeOptions: IonicBond[] = [];

  for (const anion of anions) {
    for (const cation of cations) {
      moleculeOptions.push(new IonicBond(anion, cation));
    }
  }

  const wrongAnswers = getRandomSelection(
    moleculeOptions.filter(
      (mol) => !mol.equals(molecule) && isMoleculeValid(mol)
    ),
    answerOptions - 1
  ).map((mol) => mol.getName());

  return new QuestionData({
    answer: molecule.getName(),
    prompt: ["What is the name of ", ...molecule.getFormula(), "?"],
    wrongAnswers,
  });
}

// more complex function that ensures that
// a element isn't in both the cation and anion at the same time
// prevents some real molecules from existing

// function isMoleculeValid(molecule: IonicBond): boolean {
//   const formulaSymbols: string[] = molecule
//     .getFormula()
//     .filter<string>((part): part is string => typeof part === "string");
//   return new Set(formulaSymbols).size === formulaSymbols.length;
// }

//todo ensure that things like water aren't made (hydrogen oxide)
function isMoleculeValid(molecule: IonicBond): boolean {
  return molecule.anion.symbol !== molecule.cation.symbol;
}

export function createRandomMolecule(
  anionList: Ion[],
  cationList: Ion[]
): IonicBond {
  while (true) {
    const molecule = new IonicBond(getRandom(anionList), getRandom(cationList));
    if (isMoleculeValid(molecule)) {
      return molecule;
    }
  }
}

const questionGenerators: QuestionGenerator[] = [
  createForumulaQuestion,
  createNameQuestion,
];

function getGenerator(questionTypes: QuestionChoices): QuestionGenerator {
  switch (questionTypes) {
    case QuestionChoices.FORMULAS:
      return createForumulaQuestion;
    case QuestionChoices.NAMES:
      return createNameQuestion;
    case QuestionChoices.FORMULAS_AND_NAMES:
      return getRandom(questionGenerators);
  }
}

export function createQuestion(settings: QuestionSettings): QuestionData {
  const generator = getGenerator(settings.questions);

  const anionList = [
    ...simpleAnions,
    ...(settings.ions["Polyatomic ions"] ? polyAtomicAnions : []),
  ];
  const cationList = [
    ...simpleCations,
    ...(settings.ions["Polyatomic ions"] ? polyAtomicCations : []),
    ...(settings.ions["Transition metal cations"] ? metalCations : []),
  ];

  return generator(
    createRandomMolecule(anionList, cationList),
    anionList,
    cationList
  );
}
