import {
  Box,
  Checkbox,
  FormControl,
  FormControlLabel,
  InputLabel,
  MenuItem,
  Select,
} from "@mui/material";
import { Dispatch } from "react";
import { ReducerAction, ReducerActionType } from "../App";

export enum QuestionChoices {
  FORMULAS = "Formulas",
  NAMES = "Names",
  FORMULAS_AND_NAMES = "Both formulas and names",
}

export type IonOptions = "Polyatomic ions" | "Transition metal cations";

export type QuestionSettings = {
  ions: Record<IonOptions, boolean>;
  questions: QuestionChoices;
};

type Props = {
  settings: QuestionSettings;
  dispatcher: Dispatch<ReducerAction>;
};

export function Settings(props: Props) {
  const ionOptions = (Object.keys(props.settings.ions) as IonOptions[]).map(
    (key: IonOptions, i) => (
      <FormControlLabel
        key={i}
        control={
          <Checkbox
            checked={props.settings.ions[key]}
            id={key}
            onChange={() =>
              props.dispatcher({
                type: ReducerActionType.UPDATE_ION_OPTION,
                option: key,
              })
            }
          />
        }
        label={key}
      />
    )
  );

  return (
    <Box sx={{ mx: "auto", maxWidth: 1000 }} className="settings-page page">
      <FormControl>
        <InputLabel id="quetion-types-select">Question types</InputLabel>
        <Select
          labelId="quetion-types-select"
          label="Question types"
          onChange={({ target: { value } }) =>
            props.dispatcher({
              type: ReducerActionType.UPDATE_QUESTION_OPTION,
              option: value as QuestionChoices,
            })
          }
          value={props.settings.questions}
        >
          {Object.values(QuestionChoices).map((name, i) => (
            <MenuItem key={i} value={name}>
              {name}
            </MenuItem>
          ))}
        </Select>
        <FormControl>{ionOptions}</FormControl>
      </FormControl>
    </Box>
  );
}
