import { ReactNode } from "react";

export interface FormulaTextPartObject {
  content: string;
  type: "subscript";
}

export type FormulaTextPart = string | FormulaTextPartObject;
export type FormulaText = string | FormulaTextPart[];

export class Subscript implements FormulaTextPartObject {
  type: "subscript";
  content: string;
  constructor(content: string) {
    this.type = "subscript";
    this.content = content;
  }
}

export function formulaTextToReactNode(text: FormulaText): ReactNode {
  if (typeof text === "string") {
    return text;
  } else {
    return text.map((part, i) => {
      if (typeof part === "string") {
        return part;
      } else {
        return <sub key={i}>{part.content}</sub>;
      }
    });
  }
}

export function formulaTextToSpreadAblePart(
  text: FormulaText
): FormulaTextPart[] {
  if (typeof text === "string") {
    return [text];
  } else {
    return text;
  }
}

export function symbolToFormulaText(input: string): FormulaTextPart[] {
  const normal = input.match(/[^\d]+/g) || [];
  const subscripts = input.match(/\d+/g) || [];

  const out = [];

  while (true) {
    const normalPart = normal.shift();
    if (normalPart === undefined) break;
    out.push(normalPart);
    const subscriptPart = subscripts.shift();
    if (subscriptPart === undefined) break;
    out.push(new Subscript(subscriptPart));
  }
  return out;
}
