import { Button, Typography } from "@mui/material";
import { ReactNode } from "react";
import { FormulaText, formulaTextToReactNode } from "../formulaText";

export interface QuestionDataParams {
  prompt: FormulaText;
  wrongAnswers: FormulaText[];
  answer: FormulaText;
}
export class QuestionData implements QuestionDataParams {
  readonly prompt: FormulaText;
  readonly wrongAnswers: FormulaText[];
  readonly answer: FormulaText;
  readonly correctAnswerLocation: number;
  readonly finalList: FormulaText[];

  constructor(params: QuestionDataParams) {
    this.prompt = params.prompt;
    this.wrongAnswers = params.wrongAnswers;
    this.answer = params.answer;
    this.correctAnswerLocation = Math.floor(
      Math.random() * (this.wrongAnswers.length + 1)
    );

    this.finalList = [...this.wrongAnswers];
    this.finalList.splice(this.correctAnswerLocation, 0, this.answer);
  }
}

type QuestionProps = {
  question: QuestionData;
  inputedAnswer: number | null;
  handleAnswer: (answer: number) => void;
};
export function Question(props: QuestionProps) {
  const answeredCorrect =
    props.inputedAnswer === props.question.correctAnswerLocation;
  const answers = (
    <div className="question-answers">
      {props.question.finalList.map((ans, i) => {
        const correct =
          props.inputedAnswer === i
            ? answeredCorrect
            : (props.inputedAnswer !== null &&
                !answeredCorrect &&
                props.question.correctAnswerLocation === i) ||
              null;
        return (
          <Answer
            correct={correct}
            key={i}
            handleClick={() => props.handleAnswer(i)}
          >
            {
              // correct !== null
              //   ? correct
              //     ? "Correct"
              //     : "Wrong"
              //   :
              formulaTextToReactNode(ans)
            }
          </Answer>
        );
      })}
    </div>
  );

  return (
    <div className="question">
      <Typography variant="h3" className="question-prompt">
        {formulaTextToReactNode(props.question.prompt)}
      </Typography>
      {answers}
      <Typography
        className={
          "delayed-appearance" + (props.inputedAnswer !== null ? " shown" : "")
        }
      >
        Click any button to continue
      </Typography>
    </div>
  );
}

type AnswerProps = {
  children: ReactNode;
  handleClick: () => void;
  correct: boolean | null;
};
function Answer(props: AnswerProps) {
  return (
    <Button
      variant={props.correct !== null ? "contained" : "outlined"}
      sx={{ m: 0.4 }}
      // @ts-ignore
      // style={{ "--fg": `hsl(${props.hue}, 60%, 30%` }}
      color={
        props.correct
          ? "success"
          : props.correct === false
          ? "error"
          : undefined
      }
      className="answer inverted-hover"
      onClick={() => props.handleClick()}
    >
      <Typography className="answer-text">{props.children}</Typography>
    </Button>
  );
}
