import { Box, Button, Tab, Tabs, Typography } from "@mui/material";
import { useEffect, useState } from "react";
import { useImmerReducer } from "use-immer";
import "./App.scss";
import {
  formulaTextToReactNode,
  formulaTextToSpreadAblePart,
} from "./formulaText";
import {
  AnsweredQuestion,
  HistoryPage,
  isQuestionCorrect,
} from "./history/HistoryPage";
import periodicTable from "./periodic-table.png";
import { Question, QuestionData } from "./questions/Question";
import {
  IonOptions,
  QuestionChoices,
  QuestionSettings,
  Settings,
} from "./questions/Settings";
import { createQuestion } from "./questions/generators";

const PERSISTED_STATE_LOCAL_STORAGE_KEY = "persisted_state";

type State = {
  persist: {
    settings: QuestionSettings;
    questionHistory: AnsweredQuestion[];
  };
  questions: {
    current: QuestionData;
    currentAnswer: number | null;
  };
};

enum UiTab {
  QUESTIONS = "questions",
  HISTORY = "history",
  SETTINGS = "settings",
}

function getInitialState(): State {
  const savedState = localStorage.getItem(PERSISTED_STATE_LOCAL_STORAGE_KEY);

  let persist;

  if (savedState !== null) {
    persist = JSON.parse(savedState);
  } else {
    const settings = {
      ions: {
        "Polyatomic ions": false,
        "Transition metal cations": false,
      },
      questions: QuestionChoices.FORMULAS_AND_NAMES,
    };
    persist = {
      settings,
      questionHistory: [],
    };
  }

  return {
    persist,
    questions: {
      current: createQuestion(persist.settings),
      currentAnswer: null,
    },
  };
}

export enum ReducerActionType {
  ANSWER,
  UPDATE_ION_OPTION,
  UPDATE_QUESTION_OPTION,
  NEW_QUESTION,
  CLEAR_HISTORY,
}
type AnswerAction = {
  type: ReducerActionType.ANSWER;
  answerNumber: number;
};
type IonSettingAction = {
  type: ReducerActionType.UPDATE_ION_OPTION;
  option: IonOptions;
};
type QuestionTypeAction = {
  type: ReducerActionType.UPDATE_QUESTION_OPTION;
  option: QuestionChoices;
};
type NewQuestionAction = {
  type: ReducerActionType.NEW_QUESTION;
};
type ClearHistoryAction = {
  type: ReducerActionType.CLEAR_HISTORY;
};
export type ReducerAction =
  | AnswerAction
  | IonSettingAction
  | QuestionTypeAction
  | NewQuestionAction
  | ClearHistoryAction;

function reducer(state: State, action: ReducerAction) {
  function createNewQuestion() {
    state.questions.currentAnswer = null;
    state.questions.current = createQuestion(state.persist.settings);
  }

  switch (action.type) {
    case ReducerActionType.ANSWER:
      if (state.questions.currentAnswer !== null) {
        createNewQuestion();
        break;
      }

      state.persist.questionHistory.push({
        data: state.questions.current,
        answer: action.answerNumber,
      });

      state.questions.currentAnswer = action.answerNumber;

      break;

    case ReducerActionType.UPDATE_ION_OPTION:
      state.persist.settings.ions[action.option] =
        !state.persist.settings.ions[action.option];

      createNewQuestion();
      break;

    case ReducerActionType.UPDATE_QUESTION_OPTION:
      state.persist.settings.questions = action.option;

      createNewQuestion();
      break;

    case ReducerActionType.CLEAR_HISTORY:
      state.persist.questionHistory = [];
      break;

    default:
  }

  localStorage.setItem(
    PERSISTED_STATE_LOCAL_STORAGE_KEY,
    JSON.stringify(state.persist)
  );
}

function App() {
  const [state, dispatch] = useImmerReducer<State, ReducerAction>(
    reducer,
    // @ts-ignore
    null,
    getInitialState
  );

  const [uiTab, setUiTab] = useState<UiTab>(UiTab.QUESTIONS);

  useEffect(() => {
    window.onunload = () => {
      window.scrollTo(0, 0);
    };
  }, []);

  function handleAnswer(answerNumber: number) {
    dispatch({
      type: ReducerActionType.ANSWER,
      answerNumber,
    });
  }

  return (
    <Box className="app">
      <TabList {...{ uiTab, setUiTab }} />
      <Box className="page-container">
        {uiTab === UiTab.SETTINGS && (
          <Settings settings={state.persist.settings} dispatcher={dispatch} />
        )}
        {uiTab === UiTab.QUESTIONS && (
          <Box
            sx={{ mx: "auto", maxWidth: 1000 }}
            className="question-page page"
          >
            <Question
              question={state.questions.current}
              inputedAnswer={state.questions.currentAnswer}
              {...{ handleAnswer }}
            />
            <img alt="peridic table" src={periodicTable} />
          </Box>
        )}
        {uiTab === UiTab.HISTORY && (
          <HistoryPage
            pastQuestions={state.persist.questionHistory}
            handleDelete={() =>
              dispatch({ type: ReducerActionType.CLEAR_HISTORY })
            }
          />
        )}
      </Box>
    </Box>
  );
}

export default App;

type TabListProps = {
  uiTab: UiTab;
  setUiTab: React.Dispatch<React.SetStateAction<UiTab>>;
};
function TabList(props: TabListProps) {
  const value = Object.values(UiTab).indexOf(props.uiTab);
  return (
    <Tabs
      sx={{
        position: "sticky",
        top: "0",
        zIndex: "100",
        bgcolor: "background.paper",
      }}
      value={value}
      className="tab-bar"
      centered
    >
      {Object.values(UiTab).map((tabName, i) => {
        const isSelected = props.uiTab === tabName;
        return (
          <Tab
            key={i}
            className="tab"
            onClick={() => !isSelected && props.setUiTab(tabName)}
            label={tabName}
          />
        );
      })}
    </Tabs>
  );
}

function QuestionReview({
  done,
  question,
}: {
  done: () => void;
  question: AnsweredQuestion;
}) {
  return (
    <>
      <Typography variant="h3">
        {isQuestionCorrect(question)
          ? "Correct"
          : formulaTextToReactNode([
              "Wrong, the right answer was ",
              ...formulaTextToSpreadAblePart(question.data.answer),
            ])}
      </Typography>
      <Button variant="contained" onClick={() => done()}>
        continue
      </Button>
    </>
  );
}
